class ItemOption {
    constructor(option) {
        this.src = option;
        //    
        const re = /(\w+)\s*\((.*)\)\s*/g;
        const array = re.exec(option);
        
        this.name = array[1];
        this.args = array[2].split(/\s*\,\s*/);
    }

    apply(target) {
        switch(this.name) {
            case "attack":
                target.plusAttack += Number(this.args[0]);
                break;
            case "matic":
                target.plusMagic += Number(this.args[0]);
                break;
            case "armor":
                target.plusArmor += Number(this.args[0]);
                break;
            case "health":
                target.health += Number(this.args[0]);
                target.health = Math.min(target.maxHealth, target.health);
                break;
        }
    }

    clear(target) {
        switch(this.name) {
            case "attack":
                target.plusAttack -= Number(this.args[0]);
                break;
            case "matic":
                target.plusMagic -= Number(this.args[0]);
                break;
            case "armor":
                target.plusArmor -= Number(this.args[0]);
                break;
            case "health":
                target.health -= Number(this.args[0]);
                target.health = Math.max(0, target.health);
                break;
        }
    }

    toString() {
        switch(this.name) {
            case "attack":
                return " " + this.args[0];
            case "matic":
                return " " + this.args[0];
            case "armor":
                return " " + this.args[0];
            case "health":
                return " " + this.args[0];
        }
    }
}

export default class ItemTable {
    constructor() {
    }

    init(data) {
        this.data = data;
        //   
        //   (apply) (clear)  ..
        //          
        //        
        for (const itemId in  this.data) {
            const item = this.data[itemId];
            const newOptions = [];
            if (item.options) {
                for (const option of item.options) {
                    const itemOption = new ItemOption(option);
                    newOptions.push(itemOption);
                }
            }
            item.options = newOptions;
        }
    }

    getCategory(itemId) {
        const item = this.data[itemId];
        if (!item)  {
            return Error("invalid item id:" + itemId);
        }
        return item.category;
    }

    getData(itemId) {
        return this.data[itemId];
    }
}


