//   State   .
export class State {
    constructor() {
        // State   .  Default State ,     State, Relation .
        this.states = ['idle', 'die', 'action', 'damaged', 'walk', 'groggy'];
        this.stateRelation = {
            idle: {
                idle: false,
                die: true,
                action: true,
                damaged: true,
                walk: true,
                groggy: true
            },
            die: {
                idle: false,
                die: false,
                action: false,
                damaged: false,
                walk: false,
                groggy: false
            },
            action: {
                idle: false,
                die: false,
                action: false,
                damaged: false,
                walk: false,
                groggy: false
            },
            damaged: {
                idle: true,
                die: true,
                action: false,
                damaged: false,
                walk: false,
                groggy: true
            },
            walk: {
                idle: true,
                die: true,
                action: true,
                damaged: true,
                walk: true,
                groggy: true
            },
            groggy: {
                idle: false,
                die: true,
                action: false,
                damaged: true,
                walk: false,
                groggy: false
            },
        }

        this.state = 'idle';
        this.prevState = null;
    }

    //      ?..    .  .
    change(state) {
        let success = this.stateRelation[this.state][state];
        [this.prevState, this.state] = success? [this.state, state]: [this.prevState, this.state];
        return success;
    }

    rollback(callback) {
        let success = this.prevState !== null;
        [this.prevState, this.state] = success? [this.state, this.prevState]: [this.prevState, this.state];
        return success;
    }

    //  State   State  addState  .
    add(state, entries, exits) {
        if (this.states.indexOf(state) < 0) {
            this.states.push(state);

            this.states.forEach((basicState) => {
                this.stateRelation[state][basicState] = false;
                this.stateRelation[basicState][state] = false;
            });
        }

        entries.forEach((entry) => {
            this.state[entry][state] = true;
        });

        exits.forEach((exit) => {
            this.state[state][exit] = true;
        });
    }
}


